* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body, html {
margin: 0;
}

@font-face {
  font-family: "BenthamRegular";
  src: local("BenthamRegular"),
    url(/react-photographer/static/media/BenthamRegular.b6c15d8a.ttf) format("truetype");
  font-weight: normal;
}

@font-face {
  font-family: "MontserratBold";
  src: local("MonserratBold"), 
    url(/react-photographer/static/media/MontserratBold.d14ad103.ttf) format("truetype");
  font-weight: bold;
}

@font-face {
  font-family: "MontserratLight";
  src: local("MonserratLight"), 
    url(/react-photographer/static/media/MontserratLight.a17f43cc.ttf) format("truetype");
  font-weight: light;
}

@font-face {
  font-family: "MontserratMedium";
  src: local("MonserratMedium"), 
    url(/react-photographer/static/media/MontserratMedium.aca6287f.ttf) format("truetype");
  font-weight: medium;
}

@font-face {
  font-family: "MontserratReglar";
  src: local("MonserratRegular"), 
    url(/react-photographer/static/media/MontserratRegular.34de1239.ttf) format("truetype");
  font-weight: normal;
}

@font-face {
  font-family: "MontserratSemiBold";
  src: local("MonserratSemiBold"), 
    url(/react-photographer/static/media/MontserratSemiBold.7ffeec2b.ttf) format("truetype");
  font-weight: 600;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.nav-container {
  padding: 2rem;
  display: flex;
  max-width: 100vw;
  font-family: MontserratReglar;
  align-items: center;
}

.logo-container {
  padding: 0 10% 0 10%;
  width: 375px;
}

.logo {
  width: 275px;
  height: auto;
}

.nav_menu_1 {
  display: block;
  width: 70%;
  padding-left: 20%;
}

ul {
  list-style: none;
  display: flex;
  justify-content: space-between;
}

li {
  font-size: x-large;
}

a {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

.nav-btn {
  display: none;
}

@media only screen and (max-width: 1450px) {
  li {
    font-size: small;
  }
}

@media only screen and (max-width: 980px) {
  .nav-container {
    justify-content: space-between;
  }

  .logo {
    width: 150px;
    height: auto;
  }

  .nav_menu_1 {
    display: none;
  }

  .nav-btn {
    display: block;
    cursor: pointer;
    right: 10px;
  }
