}

.center-upper-div {
    width: 60%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.footer-right > p {
    padding: 3% 0 3% 0;;
}

.footer-right > div {
    padding: 3% 0 3% 0;;
}

.footer-icons-container {
    display: flex;
}

.footer-icon {
    display: flex;
    align-items: center;
    justify-content: center;
}

.down-container {
    display: flex;
    padding: 4% 0 4% 0;
    justify-content: center;
}
.hero-container {
    max-width: 100vw;
    max-height: 100vh;
    display: flex;
    justify-content: flex-end;
    background-color: #F6F6F3;
}

.hero-left {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 60%;
    height: auto;
}

h1 {
    font-family: BenthamRegular;
